.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
li{
  list-style-type:none;
  margin-top: 15px;
  margin-bottom: 15px;
  margin-right: 200px;
  margin-left: 200px;
  border: 1px solid black;
  border-radius: 0.5rem;
}
a{
  text-decoration: none;
}
.image{
  margin-top: 10px;
}
.description{
  margin-right: 10px;
  margin-left: 10px;
}
#book-form input{
  background-color: #2A2B2DFF;
  border:0;
  padding: 10px 20px;
  margin: 10px;
  border-radius: 0.5rem;
  color: #2DA8D8FF;
}
#book-form input::placeholder{
  color: rgba(206, 206, 206, 0.589);
}
#book-form input:focus,
#book-form button:focus{
  outline: none;
}
#book-form button{
  width: 100px;
  height: 35px;
  margin: 20px;
  border: 0;
  border-radius: 0.5rem;
  background-color: #2DA8D8FF;
  color: #2A2B2DFF;
  font-weight: bolder;
  cursor: pointer;
}